<template>
  <!-- Topbar -->
  <nav class="navbar navbar-expand topbar mb-4 static-top shadow">
    <!-- Sidebar Toggle (Topbar) -->
    <button
      id="sidebarToggleTop"
      class="btn btn-link d-md-none rounded-circle mr-3"
      @click="toggleSidebar()"
    >
      <i class="fa fa-bars"></i>
    </button>

    <!-- Topbar Navbar -->
    <ul class="navbar-nav ml-auto">
      <!-- Nav Item - User Information -->
      <li class="nav-item name-user-logged">
        <div class="nav-link">{{ this.userLogged }}</div>
      </li>
      <li class="nav-item btn-sign-out" @click="signOut()">
        <a class="nav-link">Logout</a>
      </li>
    </ul>
  </nav>
  <!-- End of Topbar -->
</template>
<script>
import store from "../store/index";

export default {
  data: () => {
    return {};
  },
  computed: {
    isLogged() {
      return this.$store.getters.isLoggedIn;
    },
    userLogged() {
      return this.$store.state.userLogged;
    },
  },
  methods: {
    toggleSidebar() {
      document.getElementById("accordionSidebar").classList.toggle("toggled");
      document.getElementById("page-top").classList.toggle("sidebar-toggled");
    },
    signOut() {
      store.dispatch("signOut").then(() => {
        this.$router.replace("/auth/login");
      });
    },
  },
};
</script>
<style>
.navbar {
  background: #042331 !important;
  color: white !important;
}
.nav-item {
  color: white;
}
.btn-sign-out {
  cursor: pointer;
}
.name-user-logged {
  display: block;
}
@media only screen and (max-width: 430px) {
  .name-user-logged {
    display: none;
  }
}
</style>
