import Vue from 'vue';
import VueRouter from 'vue-router';
import config from '../config';
Vue.use(VueRouter);

const routes = [
	{
		path: '*',
		redirect: '/auth/login',
	},
	{
		path: '/',
		name: 'Home',
		meta: {
			requiresAuth: true,
		},
		component: () => import('../views/Home.vue'),
	},
	{
		path: '/solicitudes-chatbot',
		name: 'Nuevo',
		meta: {
			requiresAuth: true,
		},
		component: () => import('../views/Nuevo.vue'),
	},
	{
		path: '/admin',
		name: 'Admin',
		meta: {
			requiresAuth: true,
		},
		component: () => import('../views/AdminAccount.vue'),
	},
	{
		path: '/reportes/chatbot',
		name: 'ReportChatbot',
		meta: {
			requiresAuth: true,
		},
		component: () => import('../views/ReportChatbot.vue'),
	},
	{
		path: '/auth/login',
		name: 'Login',
		component: () => import('../views/Login.vue'),
	},
	{
		path: '/auth/register',
		name: 'Register',
		component: () => import('../views/Register.vue'),
	},
];

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes,
});

router.beforeEach((to, from, next) => {
	const user = window.localStorage.getItem('userLogged');
	const requiresAuth = to.matched.some((r) => r.meta.requiresAuth);
	if (requiresAuth) {
		if (user === null) {
			next('/auth/login');
		} else {
			next();
		}
	} else {
		next();
	}
});

export default router;
