import Vue from 'vue';
import Vuex from 'vuex';
import { firebaseApp } from '../db';
import config from '../config';
import axios from 'axios';
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		userLogged: localStorage.getItem('userLogged'),
		chartData: null,
		chartOptions: null,
		layout: 'login-layout',
		loadingData: false,
	},
	mutations: {
		SET_USER_LOGGED(state, userData) {
			localStorage.setItem('userLogged', userData);
			state.userLogged = userData;
		},
		REMOVE_USER_LOGGED(state) {
			localStorage.removeItem('userLogged');
			state.userLogged = '';
		},
		SET_LAYOUT(state, newLayout) {
			state.layout = newLayout;
		},
		SET_LOADING_DATA(state, payload) {
			state.loadingData = payload;
		},
	},
	getters: {
		isLoggedIn(state) {
			return !!state.userLogged;
		},
	},
	actions: {
		signIn({ commit }, credentials) {
			return new Promise((resolve, reject) => {
				firebaseApp
					.auth()
					.signInWithEmailAndPassword(credentials.email, credentials.password)
					.then((data) => {
						commit('SET_USER_LOGGED', data.user.email);
						resolve(true);
					})
					.catch((error) => {
						console.log('Error al iniciar sesión', error);
						reject(error);
					});
			});
		},
		signOut({ commit }) {
			return new Promise((resolve, reject) => {
				firebaseApp
					.auth()
					.signOut()
					.then(() => {
						commit('REMOVE_USER_LOGGED');
						resolve(true);
					})
					.catch((error) => {
						console.log('Error al cerrar sesión', error);
						reject(error);
					});
			});
		},
		getReportDaily({ state, commit, dispatch }, data) {
			return new Promise((resolve, reject) => {
				const infoQuery = {
					emailUser: state.userLogged,
					dateStart: data.dateStart,
					dateEnd: data.dateEnd,
				};
				axios
					.post(config.RUTA_BACKEND_CHATBOT + 'reports/email/daily', infoQuery)
					.then((result) => {
						if (!result.data.success) {
							reject(result.data.message);
						}
						console.log(result)

						const rta = result.data.message.result.map((item) => {
							return {
								date: item._id.substring(0, 10),
								count: item.count,
							};
						});
						commit("SET_LOADING_DATA", false);
						resolve(rta);
					})
					.catch((error) => {
						reject(error);
					});
			});
		},
		getReportMonthly({ state, commit, dispatch }, data) {
			return new Promise((resolve, reject) => {
				const infoQuery = {
					emailUser: state.userLogged,
					dateStart: data.dateStart,
					dateEnd: data.dateEnd,
				};
				axios
					.post(config.RUTA_BACKEND_CHATBOT + 'reports/email/monthly', infoQuery)
					.then((result) => {
						if (!result.data.success) {
							reject(result.data.message);
						}

						const rta = result.data.message.result.map((item) => {
							return {
								month: item._id.substring(0, 7),
								count: item.count,
							};
						});
						commit("SET_LOADING_DATA", false);
						resolve(rta);
					})
					.catch((error) => {
						reject(error);
					});
			});
		},
		getReportIntents({ state, commit, dispatch }, data) {
			return new Promise((resolve, reject) => {
				const infoQuery = {
					emailUser: state.userLogged,
					dateStart: data.dateStart,
					dateEnd: data.dateEnd,
				};
				axios
					.post(config.RUTA_BACKEND_CHATBOT + 'reports/chatbot/intents', infoQuery)
					.then((result) => {
						if (!result.data.success) {
							reject(result.data.message);
						}

						const rta = result.data.message.result.map((item) => {
							return {
								intent: item._id,
								count: item.count,
							};
						});
						commit("SET_LOADING_DATA", false);
						resolve(rta);
					})
					.catch((error) => {
						reject(error);
					});
			});
		},
		getAccountInfo({ state, commit, dispatch }, data) {
			return new Promise((resolve, reject) => {
				axios
					.get(config.RUTA_BACKEND_CHATBOT + `accounts/${state.userLogged}`)
					.then((rta) => {
						commit("SET_LOADING_DATA", false);
						resolve(rta.data.message.account);
					})
					.catch((error) => {
						reject(error.message);
					});
			});
		},
		updateAccountInfo({ state, commit, dispatch }, data) {
			return new Promise((resolve, reject) => {
				axios
					.put(config.RUTA_BACKEND_CHATBOT + `accounts/${state.userLogged}`, data)
					.then((rta) => {
						commit("SET_LOADING_DATA", false);
						resolve(rta.data.message.account);
					})
					.catch((error) => {
						reject(error);
					});
			});
		},
	},
	modules: {},
});
